import React, {Component} from 'react'
import Grid from '@material-ui/core/Grid'
import styles from './MainPage.module.css'
import { I18n } from 'aws-amplify';
import InfoPane from '../../components/Main/InfoPane/InfoPane'
import ImagePane from '../../components/Main/ImagePane/ImagePane'
import MobileStepper from '@material-ui/core/MobileStepper';
import pastaImg from '../../assets/main_floor.jpg'
import floorImg from '../../assets/main_pasta.jpg'
import NavigateNext from '@material-ui/icons/NavigateNext'
import NavigateBefore from '@material-ui/icons/NavigateBefore'
import IconButton from '@material-ui/core/IconButton'
import borderImg from '../../assets/main_info_border.svg'
import Penne from '../../components/ui/Icons/Penne/Penne'
import Farfalle from '../../components/ui/Icons/Farfalle/Farfalle'
import Ravioli from '../../components/ui/Icons/Ravioli/Ravioli'

const amberColor = '#FFC107'
const lightAmberColor = '#FFECB3'
const darkAmberColor = '#FF8F00'

class MainPage extends Component {
	state ={
		maxSteps: 3,
		activeStep: 0,
		prevStep: 0,
		stepList: [
			{id: 0, image: floorImg, text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam nec ante libero. Nam dignissim risus lorem, vitae euismod tellus ultricies sit amet. Maecenas malesuada at dui ut tincidunt. Nam vestibulum orci vulputate, consequat nunc et, vehicula turpis. Praesent eros neque, porttitor in urna sit amet, ornare fermentum velit. Aenean non neque nec nisl laoreet luctus. Aenean finibus lectus rhoncus lorem sagittis maximus. In consectetur consectetur ex, ut lobortis mauris faucibus id. Duis eleifend dapibus bibendum. Pellentesque bibendum, libero eget congue sodales, turpis justo gravida orci, vitae pretium enim erat a nulla. Phasellus luctus ornare erat sed tincidunt. In hac habitasse platea dictumst. Morbi porttitor, nisi interdum tempor bibendum, libero lorem aliquam nunc, a vehicula tellus eros porttitor turpis.' ,
			 header: 'O nas'},
			{id: 1, image: pastaImg, text: 'Nunc a nibh nec lacus placerat gravida sit amet in diam. Sed a urna vitae eros porta euismod quis at nibh. Ut quis tristique risus, et volutpat nisi. Integer eu orci odio. Vestibulum feugiat mauris libero, sit amet volutpat diam pretium sit amet. Nam ac diam vitae lacus luctus convallis quis eu nisi. Nulla mauris tortor, sollicitudin sit amet mattis non, accumsan id neque. Pellentesque fringilla urna vitae diam elementum eleifend. Nunc sit amet enim lacus. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Suspendisse nibh est, fermentum quis nisi ultricies, aliquam condimentum justo. Vestibulum tincidunt ligula dictum porttitor vulputate. Donec ipsum tellus, gravida sed elit non, blandit ultricies risus. Suspendisse nec nulla dapibus, efficitur nisi et, fringilla mi.',
			 header: 'Kontakt'},
			{id: 2, image: pastaImg, text: 'Aenean volutpat mauris tellus. Cras tempus sapien nisl, in accumsan neque ornare et. Nunc convallis iaculis accumsan. Curabitur at feugiat orci, tristique porttitor libero. Maecenas blandit id nunc nec posuere. Praesent sagittis metus ipsum, pretium accumsan libero semper eget. Nullam eu imperdiet dui, id lobortis odio. Cras tempus, leo eget dapibus vulputate, neque metus posuere lacus, quis ultricies ipsum felis vitae tellus. Nulla pretium tincidunt pellentesque. Nunc molestie ipsum eget venenatis laoreet.Quisque sed leo dolor. Suspendisse dictum quam dapibus enim porta, quis facilisis ex finibus. Vestibulum hendrerit rhoncus arcu sit amet mattis. Cras pellentesque ultricies volutpat. Mauris et massa condimentum massa lobortis pellentesque. Morbi rutrum vestibulum scelerisque. Quisque vulputate urna fringilla, ornare sapien a, molestie libero. Duis mattis felis nulla.',
			 header: 'Other'}
		]
	}
	componentDidMount(){
		document.documentElement.style.setProperty('--border-image-url', `url(${borderImg})`)
	}
	handleClickNext = () => {
		this.setState((prevState) => 
		({
			activeStep: prevState.activeStep === prevState.maxSteps - 1 ? 0 : prevState.activeStep + 1,
			prevStep: prevState.activeStep
		}))
	}
	handleClickPrev = () => {
		this.setState((prevState) => ({
			activeStep: prevState.activeStep === 0 ? prevState.maxSteps -1 : prevState.activeStep - 1,
			prevStep: prevState.activeStep
		}))
	}
	handleChangeIndex= activeStep => {
		this.setState((prevState) => ({
			activeStep: activeStep,
			prevStep: prevState.activeStep
		}))
	}
	render() {
		const {maxSteps, activeStep, stepList, prevStep} = this.state
		return (
			<Grid container className={styles.RootContainer}>
				<Grid item md={6} className={styles.ImagePaneContainer} >
				<ImagePane
					index={activeStep}
					items={stepList}
					handleChangeIndex={this.handleChangeIndex}
					/>
					<MobileStepper 
					steps={maxSteps}
					activeStep={activeStep} 
					position={'static'}
					nextButton={
						<IconButton onClick={this.handleClickNext}>
							<NavigateNext fontSize='large'/>
						</IconButton>
					}
					backButton = {
						<IconButton onClick={this.handleClickPrev}>
							<NavigateBefore fontSize='large'/>
						</IconButton>
					}
					/>			
				</Grid>
				<Grid item md={6} className={styles.InfoPaneContainer}>
					<InfoPane 
					prevStep={prevStep}
					activeStep={activeStep} 
					stepList={stepList}/>
					<div className={styles.IconContainer}>
						<Penne inner={darkAmberColor} outer={amberColor} style={{width: '64px'}}/>
						<Farfalle inner={darkAmberColor} outer={amberColor} style={{width: '64px'}}/>
						<Ravioli inner={darkAmberColor} outer={amberColor} style={{width: '64px'}}/>
					</div>
				</Grid>		
			</Grid>
		)
	}
}

export default MainPage
